import React from "react";
import { css } from "@emotion/core";
import { useMemo, useCallback } from "react";
import Backdrop from "./Backdrop";

const render = ({
  isOpen,
  styles,
  children,
  onModalClick,
  onBackdropClick,
  onEscape
}) => (
  <Backdrop
    isOpen={isOpen}
    onClick={onBackdropClick}
    onEscape={onEscape}
    animation={{
      initial: { translateX: 20 },
      animate: { translateX: 0 },
      exit: { opacity: 0 },
      transition: {
        type: "spring",
        mass: 1,
        damping: 12,
        stiffness: 180,
        restSpeed: 20,
        restDelta: 0.1
      }
    }}
  >
    <div className="modal" css={styles} onClick={onModalClick}>
      {children}
    </div>
  </Backdrop>
);

const Modal = ({ children, isOpen, onHide }) => {
  /** STATE */
  const styles = useMemo(() => _styles(), []);

  /** METHODS */
  const hideModal = useCallback(() => {
    if (typeof onHide === "function") {
      onHide();
    }
  }, [onHide]);

  /** HANDLERS */
  const onBackdropClick = () => {
    hideModal();
  };

  const onModalClick = ev => {
    ev.stopPropagation();
  };

  const onEscape = () => {
    hideModal();
  };

  /** EFFECTS */

  return render({
    isOpen,
    styles,
    children,
    onBackdropClick,
    onModalClick,
    onEscape
  });
};

const _styles = () => css`
  /* .modal */
  min-width: 600px;
  min-height: 300px;
  background: white;
`;

export default Modal;
